.about-me {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  max-width: 300px;
  margin: auto;
  text-align: center;
  width: 100%;
  margin: 0;
  min-height: 75vh;
  white-space: normal;
  overflow-x: scroll;
  flex-wrap: wrap;
}

.title {
  color: grey;
  font-size: 18px;
}

.card-wrapper {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  flex-wrap: wrap;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  text-align: center;
  min-width: 400px;
  cursor: default !important;
}

.flex-item {
  margin: 3%;
}

.flex-container {
  padding: 0;
  margin: 0;
  list-style: none;
  -ms-box-orient: horizontal;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -moz-flex;
  display: -webkit-flex;
  display: flex;
}

.wrap-reversez {
  -webkit-flex-wrap: wrap-reverse;
  flex-wrap: wrap;
  justify-content: center;
}

.card-text-diff {
  color: black;
  font-weight: bolder;
}

.card-text-time {
  color: black;
}

.card-text-exercises {
  color: black;
  font-size: 20px;
}

.add-button {
  width: 200px;
  background-color: #0c71e0;
  margin: 5px;
  float: right;
}
